/*
* Style Works for both Editor and Frontend
*/

// Desktop Styles
@media (min-width: 1025px) {
	.pinit-container {
		display: grid;
		grid-template-columns: 3fr 2fr;
		background: #f8f3f3;
		border-radius: 15px;
		padding: 30px 30px 0px 30px !important;
		margin-bottom: 20px;
		background-image: url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png),
			url(/includes/assets/images/search-bubbles.png);
		background-position: top right;
		background-size: auto 370px;
		overflow: hidden;

		.pinit-left {
			display: grid;
			align-items: center;
			.flexguten-pinit-card-title h4 {
				font-weight: 900;
				line-height: 1.3;
				font-family: "Merriweather", sans-serif;
				text-decoration: none;
			}

			.flexguten-pinit-card-subtitle {
				font-family: montserrat, Arial;
				letter-spacing: 0.5px;
				line-height: 1.3em;
			}

			.pinterest-button-style {
				text-decoration: none;
				.pinterest-button {
					background-image: url(/includes/assets/images/pin-btn.png);
					align-items: center;
					cursor: pointer;
					background-size: 37px;
					background-repeat: no-repeat;
					border-radius: 20px;
					display: inline-flex;
					font-size: 13px;
					text-decoration: none;
					font-weight: 700;
					height: 36px;
					letter-spacing: 1.1px;
					text-transform: uppercase;
					padding: 0 20px 0px 40px;
					& svg {
						height: 17px;
						margin-left: 10px;
					}
				}
			}
		}

		.pinit-right {
			.review-image {
				width: 100%;
				height: 100%;
				display: grid;
				align-items: end;

				& img {
					object-fit: cover;
					-o-object-fit: cover;
					display: grid;
					justify-self: center;
					margin-top: 10px;
					border: 5px solid white;
					border-radius: 15px 15px 0px 0px;
					object-position: center;
					transform: translateY(0);
					transition: transform 0.2s, -webkit-transform 0.2s !important;
					will-change: transform;
					margin-bottom: -15px;
				}
			}
		}
	}

	.pinit-container:hover .review-image {
		transform: translateY(-10px);
		transition: 0.5s;
	}
}

